@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mulish", sans-serif;
  /* overflow-y: hidden; */
}

html::-webkit-scrollbar {
  display: none;
}

.app {
  background: rgb(237, 240, 243);
}

.app hr {
  opacity: 1;
}

@media (max-width: 998px) {
  .container {
    width: calc(100vw-35vw);
    max-width: 100%;
  }
}

.navbar {
  position: fixed;
  z-index: 3;
  top: 0;
  transition-timing-function: ease-in;
  transition: all 0.3s ease-in;
}

#header {
  position: relative;
  height: 100vh;
  width: 100%;
  background: #161415 url(/portfolio-site/static/media/header-background.3b9d0a7b.jpg) no-repeat top center;
  background-size: cover !important;
  -webkit-background-size: cover !important;
  overflow: hidden;
  /* object-fit: contain; */
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  width: 85%;
  padding-bottom: 30px;
  text-align: center;
  margin-top: 4rem;
  font-weight: bold;
}

.info h1 {
  font: 80px "opensans-bold", sans-serif;
  font-weight: 600;
  color: #fff;
  letter-spacing: 1px;
  margin: 0 auto 18px auto;
  text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.8);
}

.info h3 {
  font: 22px "librebaskerville-regular", serif;
  color: #b6b4b4;
  margin: 0 auto;
  width: 66%;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.5);
  line-height: 1.6;
}

#header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}
.info span {
  color: #fff;
}

#id_home,
#id_about,
#id_education,
#id_skills,
#id_projects {
  color: rgb(255, 103, 2);
}

.navbar button {
  background-color: rgb(255, 103, 2);
  width: 42px;
  height: 38px;
}

.navbar-toggler .line {
  margin-top: 3px;
  width: 100%;
  float: left;
  height: 2px;
  background-color: #fff;
  margin-bottom: 3px;
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
}

.social a i {
  margin-top: 2.8rem;
  margin-right: 1.4rem;
  color: white;
  font-size: 1.4rem;
}

.social a i:hover {
  color: #11abb0;
  cursor: pointer;
}

.navbar-collapse a {
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  font-weight: bold;
  letter-spacing: 1.5px;
}

.navbar {
  margin-top: -6px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
  outline: none;
  box-shadow: none;
}

.navbar-collapse {
  margin-top: 12px;
}

.button {
  position: absolute;
  bottom: 0;
  left: 47%;
  right: 50%;
  width: 75px;
  height: 60px;
}

.button button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 44px;
  width: 44px;
  background-color: #fff;
  border-radius: 50%;
  border: none;
  outline: none;
  visibility: hidden;
}

.button button i {
  margin-top: 2px;
  font-size: 2rem;
}

.button button:hover {
  background-color: #11abb0;
  /* color: white; */
}

@media (min-width: 998px) {
  .navbar-collapse {
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  #header {
    text-align: center;
    background-position: top;
  }
  .button button {
    visibility: visible;
  }
  .hide {
    visibility: hidden;
  }

  .addColor {
    background-color: #363434;
    left: 0;
    height: 3.4rem;
    text-align: center;
  }
}

@media (max-width: 900px) {
  /* .navbar {
    margin-top: 0px;
  } */
  .navbar-collapse {
    background-color: #1f2024;
    max-width: 300px;
    width: 100%;
    margin-left: 5px;
    min-height: 330px;
    padding-top: 8px;
    margin-right: 10px;
    visibility: visible;
  }
  .navbar button {
    margin-right: 24px;
  }
  .navbar-collapse a {
    margin: 12px;
    font-size: 13px;
    border-bottom: 0.1px solid grey;
    padding: 12px;
    font-weight: bold;
    margin-bottom: 6px;
  }
  .info {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    width: 80%;
    margin-bottom: 30px;
    /* margin-left: 1rem; */
  }
  .info h1 {
    font-size: 36px;
    font-weight: 700;
  }
  .info h3 {
    width: 94%;
    font-size: 15px;
    padding: 0px 6px;
  }
  .social {
    margin-left: 6px;
  }
}

#about-header {
  background: #2b2b2b;
  padding-top: 65px;
  padding-bottom: 45px;
  overflow: hidden;
  height: 650px;
}

.about-info h1 {
  color: #fff;
  line-height: 1.6;
  font-size: 28px;
}

.about-info h3 {
  color: #a8a8a8;
  font-size: 16px;
  line-height: 1.5;
  width: 85%;
}

.about-row {
  display: flex;
  justify-content: space-evenly;
  margin-left: 2rem;
}

.about-info {
  display: flex;
  flex-direction: column;
  flex: 0.8 1;
  letter-spacing: 1px;
  line-height: 1.6;
  padding: 1rem 3rem 1rem 4rem;
  margin-left: 3rem;
}

.about-contact {
  margin-top: 1.5rem;
  display: flex;
}

.about-resume a {
  display: flex;
  background: #444;
  margin-top: 3rem;
  margin-left: 8rem;
  max-height: 4rem;
  align-items: center;
  display: flex;
  justify-content: center;
  border-radius: 8px;
  padding: 0 1rem;
  color: #11abb0;
  cursor: pointer;
  text-decoration: none;
}

.about-resume a p {
  font-weight: 800;
  padding-top: 1rem;
  /* border-left: 0.9px solid #111; */
}

.about-resume a:hover {
  background: #fff;
  color: #2b2b2b;
}

.about-resume a i {
  margin-right: 4px;
  cursor: pointer;
}

.image {
  flex: 0.2 1;
  /* margin: 4rem; */
  /* width: 300px; */
  height: 350px;
  /* object-fit: contain */
}

.image img {
  /* width: 100%;
  height: 100%; */
  height: 400px;
  /* position: relative; */
  border-radius: 25%;
  /* overflow: hidden; */
  /* object-fit: contain; */
}

@media (max-width: 900px) {
  #about-header {
    /* padding-top: 2rem; */
    padding: 0 1rem;
    height: 850px;
  }

  .about-row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* padding: 0; */
    margin: 0;
  }
  .image {
    width: 180px;
    height: 220px;
    margin-left: -10rem;
    margin-bottom: 1rem;
  }

  .image img {
    height: 240px;
  }

  .about-info {
    display: flex;
    flex-direction: column;
    /* justify-content: start; */
    /* padding: 1rem 3rem 1rem 0rem; */
    margin-left: -5rem;
  }
  .about-info h3 {
    width: 100%;
  }
  .about-info h1 {
    width: 100%;
    font-size: 27px;
  }
  .about-resume a {
    margin-top: 20px;
    margin-bottom: -12px;
    margin-left: 0.1rem;
